"use client";

import { useState, useEffect, useRef, useCallback } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { Avatar, AvatarFallback, AvatarImage } from "~/components/ui/avatar";
import { Button } from "~/components/ui/button";
import { Badge } from "~/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "~/components/ui/tabs";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "~/components/ui/dropdown-menu";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "~/components/ui/dialog";
import { api } from "~/lib/trpc/client";
import { PostCard } from "~/components/feed/PostCard";
import { GridView } from "~/components/feed/GridView";
import { formatRelativeTime, getInitials } from "~/lib/utils";
import { ExternalLink, Users, List, LayoutGrid, Loader2, FolderKanban, MoreHorizontal, Pencil, Trash2 } from "lucide-react";
import { useSession } from "next-auth/react";

interface ProjectDetailProps {
  project: {
    id: string;
    name: string;
    description: unknown;
    coverUrl: string | null;
    createdAt: Date;
    createdById: string;
    urls: Array<{
      id: string;
      title: string;
      url: string;
    }>;
    members: Array<{
      user: {
        id: string;
        displayName: string;
        avatarUrl: string | null;
      };
    }>;
    _count: {
      posts: number;
    };
  };
}

export function ProjectDetail({ project }: ProjectDetailProps) {
  const router = useRouter();
  const { data: session } = useSession();
  const [viewMode, setViewMode] = useState<"feed" | "grid">("feed");
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const loadMoreRef = useRef<HTMLDivElement>(null);

  const isOwner = session?.user?.id === project.createdById;
  const isAdmin = session?.user?.role === "ADMIN" || session?.user?.role === "OWNER";
  const canModify = isOwner || isAdmin;

  const deleteMutation = api.project.delete.useMutation({
    onSuccess: () => {
      router.push("/projects");
    },
  });

  const handleDelete = () => {
    deleteMutation.mutate({ id: project.id });
  };

  const {
    data,
    fetchNextPage,
    hasNextPage,
    isFetchingNextPage,
    isLoading,
  } = api.post.byProject.useInfiniteQuery(
    { projectId: project.id, limit: 10 },
    {
      getNextPageParam: (lastPage) => lastPage.nextCursor,
    }
  );

  const handleObserver = useCallback(
    (entries: IntersectionObserverEntry[]) => {
      const [target] = entries;
      if (target?.isIntersecting && hasNextPage && !isFetchingNextPage) {
        fetchNextPage();
      }
    },
    [fetchNextPage, hasNextPage, isFetchingNextPage]
  );

  useEffect(() => {
    const element = loadMoreRef.current;
    if (!element) return;

    const observer = new IntersectionObserver(handleObserver, {
      root: null,
      rootMargin: "100px",
      threshold: 0,
    });

    observer.observe(element);
    return () => observer.disconnect();
  }, [handleObserver]);

  const posts = data?.pages.flatMap((page) => page.posts) ?? [];

  return (
    <div className="space-y-8">
      {/* Header */}
      <header className="space-y-6">
        {project.coverUrl && (
          <div className="aspect-[3/1] overflow-hidden rounded-xl">
            <img
              src={project.coverUrl}
              alt={project.name}
              className="h-full w-full object-cover"
            />
          </div>
        )}

        <div className="flex items-start justify-between">
          <div className="flex items-center gap-4">
            {!project.coverUrl && (
              <div className="flex h-16 w-16 items-center justify-center rounded-xl bg-primary/10">
                <FolderKanban className="h-8 w-8 text-primary" />
              </div>
            )}
            <div>
              <h1 className="text-3xl font-bold">{project.name}</h1>
              <p className="text-sm text-muted-foreground">
                Created {formatRelativeTime(new Date(project.createdAt))} ·{" "}
                {project._count.posts} posts
              </p>
            </div>
          </div>
        </div>

        {/* Links */}
        {project.urls.length > 0 && (
          <div className="flex flex-wrap gap-2">
            {project.urls.map((url) => (
              <a
                key={url.id}
                href={url.url}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Badge variant="secondary" className="gap-1">
                  {url.title}
                  <ExternalLink className="h-3 w-3" />
                </Badge>
              </a>
            ))}
          </div>
        )}

        {/* Members */}
        {project.members.length > 0 && (
          <div className="flex items-center gap-2">
            <Users className="h-4 w-4 text-muted-foreground" />
            <div className="flex -space-x-2">
              {project.members.slice(0, 5).map(({ user }) => (
                <Link key={user.id} href={`/user/${user.id}`}>
                  <Avatar className="h-8 w-8 border-2 border-background">
                    <AvatarImage src={user.avatarUrl ?? undefined} />
                    <AvatarFallback className="text-xs">
                      {getInitials(user.displayName)}
                    </AvatarFallback>
                  </Avatar>
                </Link>
              ))}
            </div>
            {project.members.length > 5 && (
              <span className="text-sm text-muted-foreground">
                +{project.members.length - 5} more
              </span>
            )}
          </div>
        )}
      </header>

      {/* Posts */}
      <section className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold">Posts</h2>
          <Tabs value={viewMode} onValueChange={(v) => setViewMode(v as "feed" | "grid")}>
            <TabsList>
              <TabsTrigger value="feed" className="gap-2">
                <List className="h-4 w-4" />
              </TabsTrigger>
              <TabsTrigger value="grid" className="gap-2">
                <LayoutGrid className="h-4 w-4" />
              </TabsTrigger>
            </TabsList>
          </Tabs>
        </div>

        {isLoading ? (
          <div className="flex justify-center py-8">
            <Loader2 className="h-6 w-6 animate-spin text-muted-foreground" />
          </div>
        ) : posts.length === 0 ? (
          <p className="py-8 text-center text-sm text-muted-foreground">
            No posts in this project yet
          </p>
        ) : viewMode === "feed" ? (
          <div className="space-y-4">
            {posts.map((post) => (
              <PostCard key={post.id} post={post} />
            ))}
          </div>
        ) : (
          <GridView posts={posts} />
        )}

        <div ref={loadMoreRef} className="flex justify-center py-4">
          {isFetchingNextPage && (
            <Loader2 className="h-6 w-6 animate-spin text-muted-foreground" />
          )}
        </div>
      </section>
    </div>
  );
}
